@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

#logo {
  animation: fill 1s ease-in-out forwards 2.6s;
}

@keyframes fill {
  0% {
    fill: transparent;
  }
  100% {
    fill: white;
  }
}

#logo path:nth-child(1) {
  stroke-dasharray: 3657;
  stroke-dashoffset: 3657;
  animation: strokeLine 6s ease-in-out forwards;
}
#logo path:nth-child(2) {
  stroke-dasharray: 1065;
  stroke-dashoffset: 1065;
  animation: strokeLine 4s ease-in-out forwards;
}
#logo path:nth-child(3) {
  stroke-dasharray: 2709;
  stroke-dashoffset: 2709;
  animation: strokeLine 4s ease-in-out forwards;
}
#logo path:nth-child(4) {
  stroke-dasharray: 2293;
  stroke-dashoffset: 2293;
  animation: strokeLine 4s ease-in-out forwards;
}

@keyframes strokeLine {
  100% {
    stroke-dashoffset: 0;
    stroke-width: 0;
  }
}

.main_div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #fff;
}

.thumbnails {
  margin: 2rem 0;
}
.thumbnails_h1,
.about_h1,
.ids_h1,
.admin_h1,
.posters_h1 {
  text-align: center;
  border-bottom: 1px solid #000;
  padding-bottom: 0.5rem;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    45deg,
    #2c5364,
    #203a43,
    #0f2027
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    45deg,
    #2c5364,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  background-size: cover;
  z-index: -10;
}

.navbar-brand img {
  max-width: 15rem;
  filter: invert(1);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header p {
  font-size: 1.5rem;
}

/* thumbnails =================================== */

.thumbnails,
.posters {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 2rem;
}
.thumb_img {
  position: relative;
}

.thumb_img a {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  text-decoration: none;
  color: #000;
  background: #fff;
  padding: 0.2rem 1rem;
  border-radius: 20px;
  transition: all 0.3s ease;
}

.thumbnail,
.poster {
  box-shadow: 0.2rem 0.2rem 2rem rgba(0, 0, 0, 0.3);
}

.thumbnail img,
.poster img {
  width: 100%;
}

.thumbnail .details,
.poster .details {
  padding: 1rem;
}

#about a {
  font-size: 0.96rem;
}

footer {
  background: #f1f1f1;
}
footer p {
  padding: 1rem;
  margin: 0;
}

@media (max-width: 768px) {
  #logo {
    width: 15rem;
    height: 8rem;
  }
  .main_div {
    min-height: calc(100vh - 79.22px);
  }
  /* thumbnails =========================== */
  .thumbnails,
  .posters {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  .navbar-brand img {
    max-width: 11rem;
  }
}
